import { Component, OnInit, Input, Inject } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Skill } from 'src/app/models/Skill';
import { SkillService } from 'src/app/service/skill.service';
import { ProjectService } from 'src/app/service/project.service';
import { User } from 'src/app/models/User';
import { UserService } from 'src/app/service/user.service';
import { MatSnackBar, MatDialog, MatDialogRef } from '@angular/material';
import { Router } from '@angular/router';
import { MAT_DIALOG_DATA } from '@angular/material';
import { Project } from 'src/app/models/Project';
import { ResTpl } from 'src/app/models/ResTpl';

@Component({
  selector: 'app-project-edit',
  templateUrl: './project-edit.component.html',
  styleUrls: ['./project-edit.component.scss']
})
export class ProjectEditComponent implements OnInit {
  projectForm: FormGroup;
  skills: []
  cycles = [
    { name: '1', value: 1 },
    { name: '3', value: 3 },
    { name: '5', value: 5 },
    { name: '7', value: 7 },
    { name: '1', value: 30 },
    { name: '3', value: 90 },
    { name: '5', value: 150 },
    { name: '7', value: 210 },
  ]

  constructor(
    private fb: FormBuilder,
    private skillService: SkillService,
    private projectSrv: ProjectService,
    private userSrv: UserService,
    public snackBar: MatSnackBar,
    public dialog: MatDialog,
    private router: Router,
  ) { }

  ngOnInit() {
    // 
    this.handleGetSkills()
    this.initForm()
  }
  // skill
  handleGetSkills() {
    this.skillService.getSkills().subscribe((res: ResTpl) => {
      this.skills = res.data
    })
  }
  // 
  initForm() {
    this.projectForm = this.fb.group({
      title: ['', [
        Validators.required
      ]],
      desc: ['', [
        Validators.required
      ]],
      skills: [[]],
      cycle: ['', [
        Validators.required
      ]],
      price: ['', [
        Validators.required
      ]]
    })
    // this.projectForm = this.fb.group({
    //   title: ['', [
    //     Validators.required
    //   ]],
    //   desc: ['', [
    //     Validators.required
    //   ]],
    //   skills: [[]],
    //   cycle: ['12', [
    //     Validators.required
    //   ]],
    //   price: ['2', [
    //     Validators.required
    //   ]]
    // })
  }
  onSubmit() {
    const data = this.projectForm.value
    if (data.skills) {
      data.skills = data.skills.join(',')
    }
    this.projectSrv.addProject(data).subscribe((res: ResTpl) => {
      console.log('res: ', res);
      this.dialog.open(addProjectSuccessDialog, {
        data: res.data
      });
    })
  }
}


/**
 * @description dialog
 */
@Component({
  selector: 'add-project-success-dialog',
  template: `
    <h2 mat-dialog-title></h2>
    <div mat-dialog-content>
      <p></p>
    </div>
    <div mat-dialog-actions>
      <button mat-button (click)="onCancel()"></button>
      <button mat-button [mat-dialog-close]="true" (click)="onOk()" cdkFocusInitial></button>
    </div>
  `,
  styleUrls: ['./project-edit.component.scss']
})
export class addProjectSuccessDialog {

  constructor(
    public dialogRef: MatDialogRef<addProjectSuccessDialog>,
    private router: Router,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) { }

  onCancel() {
    this.dialogRef.close();
    this.router.navigate(['/home/projects'])
  }
  onOk() {
    console.log('this.id: ', this.data);
    this.router.navigate(['/sub/project', this.data._id], {
      replaceUrl: true
    })
  }
}

